.locations {
    .section-title {
        margin-bottom: 30px;
    }
    @media (max-width: 1300px) {
        .section-title {
            margin-bottom: 19px;
            font-size: 32px;
            line-height: 140%;
        }
    }
    @media (max-width: 650px) {
        padding-top: 36px;
        .section-title {
            margin-bottom: 19px;
            font-size: 24px;
            line-height: 120%;
            max-width: 77%;
        }
    }
}

.location__desc {
    margin-bottom: 48px;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    @media (max-width: 1300px) {
        font-size: 14px;
        margin-bottom: 24px
    }
    @media (max-width: 992px) {
        font-size: 12px;
    }
    @media (max-width: 650px) {
        margin-bottom: 16px;
        line-height: 160%;
    }
}

#map {
    height: 540px;
    width: 100%;
    @media (max-width: 1300px) {
        height: 400px;
    }
    @media (max-width: 650px) {
        .ymaps-2-1-79-map>.ymaps-2-1-79-map,
        .ymaps-2-1-79-map>.ymaps-2-1-79-map>.ymaps-2-1-79-inner-panes {
            border-radius: var(--radius-border);
        }
    }
}

.clusterIcon {
    background: #000;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    position: relative;
    &:after {
        content: attr(data-title);
        position: absolute;
        right: 40px;
    }
}