.bayan {
    overflow: hidden;
    transition: all 0.4s;
}

.bayan__top {
    cursor: pointer;
    position: relative;
    z-index: 2;
}

.bayan__bottom {
    transition: all 0.4s;
    opacity: 0;
    position: relative;
    z-index: 1;
    overflow: hidden;
    width: 100%;
    max-height: 0;
}

.bayan__bottom--absolute {
    position: absolute;
}

.bayan--opened .bayan__bottom {
    margin-top: 0;
    opacity: 1;
}

.bayan--opened .bayan__toggler {
    transform: rotate(180deg);
    transform-origin: center;
}

@media (max-width: 1199px) {
    .bayan__bottom--mobile-static {
        position: static;
    }
}